import React, {Component} from "react"

export default class Person extends Component {
    constructor() {
        super()
        this.state = {
            person: null,
        }

        this.fetchData = this.fetchData.bind(this)
    }

    fetchData() {
        fetch("https://api.randomuser.me/")
            .then(request => request.json())
            .then(data => this.setState({
                person: data.results[0] 
            }))
            .catch(function() {console.log("Data Denied")})
    }

    render() {
        const person = this.state.person
        const lorem_ipsum = `Lorem ipsum dolor sit amet, consectetur adipiscing elit, 
                            sed eiusmod tempor incidunt ut labore et dolore magna aliqua. 
                            Ut enim ad minim veniam, quis nostrud exercitation ullamco 
                            laboris nisi ut aliquid ex.`

        return (
            <div className="container">
                <div className="randomizer-container">
                    <div className="title">
                        <h2>PERSON RANDOMIZER</h2>
                        <div className="button_container">
                            <button onClick={this.fetchData} className="random_button">Randomize!</button>
                        </div>
                    </div>
                    {this.state.person ?
                        <div className="grid-container"> 
                            <img className="photo" src={person.picture.large} />
                            <div className="name">{person.name.first} {person.name.last}</div>
                            <div className="email">{person.email}</div>
                            <div className="age">{person.dob.age}yo</div>
                            <div className="country">From {person.location.country}</div>
                            <div className="lorem-ipsum">{lorem_ipsum}</div>
                        </div>
                    : 
                        <h3>Click "Randomize!" for a random person. </h3>}      
                </div>
            </div>
        )
    }
}

